import React, { useState, useEffect } from 'react';
import ReactDOM from "react-dom";
import styles from "assets/jss/material-kit-react/views/componentsSections/typographyStyle.js";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles(styles);
export default function InstagramThumnails(props) {
   const classes = useStyles();
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [items, setItems] = useState([]);

  // Note: the empty deps array [] means
  // this useEffect will run once
  // similar to componentDidMount()
  useEffect(() => {
    fetch("https://graph.facebook.com/v10.0/instagram_oembed?url=https://www.instagram.com/p/"+props.imageBase+"/&access_token=253751762914831|1b8637440227e6f24ea29414a36aca5e")
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
          setItems(result);
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
  }, [])
console.log(items)
  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded) {
    return <div>Loading...</div>;
  } else {

    return (
       <div>
                  <img
                      src={items.thumbnail_url}
                      alt="..."
                      className={classes.imgRounded + " " + classes.imgFluid}
                    />

        </div>
    )
  }
}
