import React, { lazy, useEffect, useState } from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import Header from "components/Header/Header.js";
import Footer from "components/Footer/Footer.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
//import styles from "assets/estheticsbyev/views/servicePageStyles.js";
import styles from "assets/jss/material-kit-react/views/landingPage.js";
import Parallax from "components/Parallax/Parallax.js";
import LipDuoService from "components/services/LipDuoService.js";
import HighFrequencyService from "components/services/HighFrequencyService.js";
import GalvanicEnergyService from "components/services/GalvanicEnergyService.js";
import ClassicFacialService from "components/services/ClassicFacialService.js";
import BoosterFacialService from "components/services/BoosterFacialService.js";
import SignatureFacialService from "components/services/SignatureFacialService.js";
const useStyles = makeStyles(styles);
const dashboardRoutes = [];



export default function Services(props) {
    const classes = useStyles();
    const service = props.service
    var footerType=false
    const { ...rest } = props;
    return (
    <div>
      <Header
        color="transparent"
        routes={dashboardRoutes}
        brand="Estheticsbyev"
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 400,
          color: "white"
        }}
        {...rest}
      />

      <Parallax small>
        <div className={classes.container}>
        {(() => {
            if(service==='LipDuo'){
              return (<LipDuoService section="title"/>)
            }
            else if(service==='HighFrequency'){
              return(<HighFrequencyService section="title"/>)
            }
            else if(service==='GalvanicEnergy'){
              return(<GalvanicEnergyService section="title"/>)
            }
            else if(service==='ClassicFacial'){
              return(<ClassicFacialService section="title"/>)
            }
            else if(service==='SignatureFacial'){
              return(<SignatureFacialService section="title"/>)
            }
            else if(service==='BoosterFacial'){
              return(<BoosterFacialService section="title"/>)
            }

          }
        )()}
        </div>
      </Parallax>
      <div className={classNames( classes.main,classes.serviceContainer)}>
        <div className={classes.container}>
        {(() => {
            if(service==='LipDuo'){
              footerType=true
              return (<LipDuoService section="body"/>)
            }
            else if(service==='HighFrequency'){
              return(<HighFrequencyService section="body"/>)
            }
            else if(service==='GalvanicEnergy'){
              return(<GalvanicEnergyService section="body"/>)
            }
            else if(service==='ClassicFacial'){
              return(<ClassicFacialService section="body"/>)
            }
            else if(service==='SignatureFacial'){
              return(<SignatureFacialService section="body"/>)
            }
            else if(service==='BoosterFacial'){
              return(<BoosterFacialService section="body"/>)
            }
          }
        )()}
        </div>
      </div>
      <Footer footerAbs={footerType}/>
    </div>
  );
}
